<!-- 博客管理页面 -->

<template>
    <el-row>
        <el-col v-for='(blog, index) in form' :key="index">
            <el-card :body-style="{ padding: '20px' }" >
                <div style="padding: 14px;">
                    <h1>{{ blog.name }}</h1><br>
                    <span>{{ blog.userName }}</span><br>
                    <el-button type="primary" @click="deleteBlog(blog.id)">删除</el-button>
                </div>
            </el-card>
        </el-col>
    </el-row>
</template>

<script>
import axios from 'axios';
const options = {
    inject: ['reload'],
    data() {
        return {
            form: {

            }
        }
    },
    methods: {
        selectBlog() {
            axios.request({
                baseURL: "http://localhost:8090/",
                url: "/blog/select",
                method: 'post',
                headers: { 'Content-Type': 'application/json', 'X-Requested-With': 'XMLHttpRequest' }
            })
                .then(res => {
                    this.form = res.data;
                });
        },
        deleteBlog(id){
            axios.request({
                baseURL: "http://localhost:8090/",
                url: "/blog/deleteBlog",
                method: 'post',
                data: id,
                headers: { 'Content-Type': 'application/json', 'X-Requested-With': 'XMLHttpRequest' }
            })
                .then(res => {
                    alert(res.data);
                    this.reload();
                });
        },
    },
    mounted: function () {
        this.selectBlog();
    }
}
export default options;

</script>